#tooltip {
    position:absolute;
    display:none;
    border:grey solid 1px;
    background:white;
}
#cal1{
    position:absolute;
    height:0px;
    width:0px;
    top:100px;
    left:100px;
    overflow:none;
    z-index:-100;
}
#cal2{
    position:absolute;
    height:0px;
    width:0px;
    top:0px;
    left:0px;
    overflow:none;
    z-index:-100;
}

textarea {
    border: 2px solid black;
}

#popup {
    position: absolute;
    border: black solid 1px;
    padding: 5px 5px;
    font-size: 10px;
    font-weight: 700;
    top: 43%;
    left: 40%;
    background: gray;
    z-index: 2;
}